// 区块模块
// 角色模块
const pathPre = '/section'
//  角色管理
export default {
    path: pathPre,
    component: resolve => {
        require.ensure([], () => {
            resolve(require('../views/section/Main.vue'))
        })
    },
    children: [
        {
            path: 'category',
            name: 'section-category',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/section/category.vue'))
                })
            },
            meta: {
                title: '区块',
                noback: true
            }
        },
        //数据管理
        {
            path: 'data',
            name: 'section-data',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/section/Index.vue'))
                })
            },
            meta: {
                title: '区块',
                noback: true
            }
        },
        {
            path: 'add',
            name: 'section-add',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/section/Form.vue'))
                })
            },
            meta: {
                title: '数据添加',
                noback: false
            }
        },
        {
            path: 'edit/:sys_id',
            name: 'section-edit',
            component: resolve => {
                require.ensure([], () => {
                    resolve(require('../views/section/Form.vue'))
                })
            },
            meta: {
                title: '数据更新',
                noback: false
            }
        },
    ]
}



